
.vux-demo {
  text-align: center;
  margin-bottom: 500px;
}
.weui-tabbar{
  position: fixed!important;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.logo {
  width: 100px;
  height: 100px
}
.child-view {
  position: absolute;
  width:100%;
  overflow: auto;
  /*padding-bottom:55px;*/
  /*padding-top:45px;*/
  top: 45px;
  bottom: 55px;
  -webkit-transition: all .4s cubic-bezier(.55,0,.1,1);
  transition: all .4s cubic-bezier(.55,0,.1,1);
}
.forward-enter, .reverse-leave-active {
  opacity: 0;
  -webkit-transform: translate(50%, 0);
  transform: translate(50%, 0);
}
.forward-leave-active, .reverse-enter {
  opacity: 0;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
}
.vux-header{
  position: fixed!important;
  width:100%;
  z-index:100;
}
body,html{
  height:100%;
  width:100%;
}



